export default{
	data(){
		return{
			list:{},
			url:this.url1,
			pinglunArr:[]
		}
	},
	methods:{
		f1(){
			uni.navigateBack({
				
			})
		},
		ping:function(){
			let that=this;
			let usurl1=that.url1+"/dinghai/comment/comment/selectOrder";
			uni.request({
			    url: usurl1,
				header:{
					"Content-Type" : "application/x-www-form-urlencoded"
				},
				method:'POST',
			    data: {
					postedId:that.list.postedId
				},
			    success: (res) => {
					that.pinglunArr=res.flag
			    },
				fail : function(err){
					console.log(err);
				}
			})
		}
	},
	onLoad(it){
		this.list=JSON.parse(it.id)
		console.log(this.list)
		this.ping()
	},
	onShow(){
		
	},
	onPullDownRefresh(){
		uni.stopPullDownRefresh()
	},
}
